@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;800;900&display=swap);
header{
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    background-color:#101820FF;
    height: 80px;
    width: 100%;
    padding: 0px 30px;
}

/*---------- logo-----------  */
header .logo{
    font-size: 2em;
}

/*---------- centering elements-----------  */
header ul,header nav{
    display: flex;
    justify-content: center;
    text-align: center;
}

header nav ul li,header .logo{
    display: inline-block;
    padding: 20px;
    cursor: pointer;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
header nav ul li a{
    font-size: 1em;
    list-style: none;
    text-decoration: none;
    font-size: 1.1rem;
    color: white;
}
input,label{
    display: none;
}

/* hover  */
nav ul li a:hover{
    color: #606060FF;
}

/*---------- Media query-----------  */
@media screen and (max-width: 1200px){
    label{
        visibility: hidden;
    }
}

/*---------- Media query for ipad-----------  */

@media screen and (max-width: 768px){
    label{
        visibility: visible;
        z-index: 99;
        font-size: 1.8em;
    }
    header .logo,header label{
        font-size: 1.8em;
        display: inline-block;
        padding: 20px 0px;
        cursor: pointer;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    header nav{
        position: absolute;
        z-index: 1;
        left: -100%;
        width: 100%;
        height: 100vh;
        opacity: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #101820FF;
        transition: 0.5s;
        -webkit-transition: 0.5s;
        -moz-transition: 0.5s;
        -ms-transition: 0.5s;
        -o-transition: 0.5s;
}
    header nav ul{
        display: block;
        /* padding-top: 50%; */
    }
    header nav ul li a{
        font-size: 1rem;
}
    header input:checked~nav{
        opacity: 1;
        left: 0%;
}
    header input:checked~nav::before{
        content: "\f00d";
    }
}


/*---------- Media query for mobile-----------  */

@media screen and (max-width: 400px) {
    header nav{
        height: 700px !important;
}
    header nav ul li a{
        font-size: 1.2em;
    }
    header .logo{
        font-size: 1.6em;
    }
}
.container{
    box-sizing: border-box;
    width: 100%;
    background-color: #FEE715FF;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 3% 8%;
}

.container .content h1{
    text-transform: capitalize;
    font-size: 4rem;
    font-weight: 600;
    color: white;
}

p{
    line-height: 1.5;
    color: #606060FF;
}

.container .content p{
    font-size: 1.2rem;
    padding: 20px 0px;
}
.container .content button{
    cursor: pointer;
    background-color: #101820FF;
    color: white;
    font-size: 1.2rem;
    font-weight: 500;
    padding: 15px 55px;
    border: none;
    transition: all 0.3s;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
}


.container .image img{
    width: 40vw;
    animation: float 3s infinite;
    -webkit-animation: float 3s infinite;
}

@-webkit-keyframes float{
    0%,100%{
        -webkit-transform: translateY(0rem);
                transform: translateY(0rem);
    }
    50%{
        -webkit-transform: translateY(2rem);
                transform: translateY(2rem);
}
}

@keyframes float{
    0%,100%{
        -webkit-transform: translateY(0rem);
                transform: translateY(0rem);
    }
    50%{
        -webkit-transform: translateY(2rem);
                transform: translateY(2rem);
}
}

/* hover  */
.container .content button:hover{
    background-color: white;
    color: #101820FF;
}

/* media queery for ipad  */
@media screen and (max-width: 768px) {

    .container{
        padding: 8% 8%;
    }
    .container .content h1{
        font-size: 2.3rem;
    }
    .container .content p{
        font-size: 1rem;
    }
    .container .content button{
        padding: 10px 25px;
        font-size: 1rem;
    }
}

/* media query for mobile  */
@media screen and (max-width: 400px) {
    .container{
        height: 100vh;
        display: block;
        padding: 50px 8% 0px;
    }
    .container .content h1{
        font-size: 2.5rem;
    }
    .container .content p{
        font-size: 1rem;
    }
    .container .image img,.container .content
    {
        width: 100%;
    }
}
.specs-section{
    padding: 8% 0%;
}

.heading{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

h2{
    font-size: 30px;
    font-weight: 400;
}

/* heading or title */
h2 span{
    font-size: 50px;
    color: #606060FF;
    font-weight: 900;
}

.specs-container{
    display: flex;
    padding: 4% 8%;
    align-items: center;
    color: #101820FF;
    position: relative;
}

.specs-container .specs-features{
    width: 100%;
}

.specs-container .specs-image img{
    position: relative;
    top: 60px;
}


.specs-container .specs-features .specs-data{
    position: relative;
    width: 100%;
    padding: 20px 0px;
    line-height: 1.5;
}

.specs-container .specs-features .specs-data i{
    font-size: 2.5rem;
}

.specs-container .specs-features .specs-data h3{
    font-size: 1.4rem;
}

.specs-data:nth-child(2),.specs-data:nth-child(3){
    margin-left: 50px;
}

/* media query for ipad */
@media screen and (max-width: 768px) {
    /* heading  */
    h2{
        font-size: 20px;
        font-weight: 400;
    }
    /* heading */
    h2 span{
        font-size: 30px;
        color: #606060FF;
        font-weight: 900;
    }

    .specs-container .specs-image img{
        position: absolute;
        width: 50%;
        top: 100px;
        right: 100px;
    }

    .specs-container .specs-features .specs-data{
        position: relative;
        width: 100%;
        padding: 20px 0px;
        line-height: 1.3;
    }

    .specs-container .specs-features .specs-data i{
        font-size: 1.5rem;
    }

    .specs-container .specs-features .specs-data h3{
        font-size: 1rem;
    }

    .specs-container .specs-features .specs-data span{
        font-size: 12px;
    }

    .specs-data:nth-child(2),.specs-data:nth-child(3){
        margin-left: 0px;
    }
    .specs-data:nth-child(1),.specs-data:nth-child(4){
        margin-left: 50px;
    }
}

/* media query for mobile */
@media screen and (max-width: 400px){
    .specs-container .specs-image img{
        width: 80%;
        top: 100px;
        right: 0;
    }
    .specs-container .specs-features .specs-data i{
        font-size: 1.2rem;
    }

    .specs-container .specs-features .specs-data h3{
        font-size: 0.8rem;
    }
    .specs-container .specs-features .specs-data span{
        font-size: 10px;
    }
}
.price-container{
    box-sizing: border-box;
    padding: 8% 8%;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 5.5rem 1.5rem;
    gap: 5.5rem 1.5rem;
}

.price-container .card{
    position: relative;
    height: 300px;
    width: 100%;
    background-color: #101820FF;
    padding: 2rem 1rem 2rem;
    border-radius: 20px;
}

.card-data{
    text-align: center;
    -webkit-transform: translateY(10rem);
            transform: translateY(10rem);
}

.price-container .card img{
    position: absolute;
    width: 305px;
}


.price-container .card h3{
    font-weight: 400;
}

.price-container .card h3,.price-container .card span{
    display: inline-block;
    position: relative;
    color: white;
    width: 100%;
    font-size: 1.6rem;
    line-height: 2rem;
    opacity: 0;
}

.card .card-data button{
    padding: 5px 40px;
    border: none;
    border-radius: 50px;
    background-color: #FEE715FF;
    opacity: 0;
    cursor: pointer;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
}

/* button icon size  */
.card .card-data button i{
    font-size: 1.5rem;
}

/* transitions  */

.card  img,
.card .card-data button,
.card .card-data h3,
.card .card-data span{
    line-height: 2;
    transition: 0.5s;
}

/* hover effect  */

.card:hover img{
    -webkit-transform: translateY(-50%)rotate(-20deg);
            transform: translateY(-50%)rotate(-20deg);
}

.card:hover .card-data h3{
    opacity: 1;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.card:hover .card-data span{
    opacity: 1;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.card .card-data button:hover{
    background-color: white;
    color: #101820FF;
}
.card:hover .card-data button{
    opacity: 1;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

/* media query for ipad  */
@media screen and (max-width: 768px) {
    .price-container .card{
        width: 200px;
        height: 200px;
    }

    .card-data{
        text-align: center;
        transform: translateY(5.5rem);
        -webkit-transform: translateY(5.5rem);
        -moz-transform: translateY(5.5rem);
        -ms-transform: translateY(5.5rem);
        -o-transform: translateY(5.5rem);
}

    .price-container .card img{
        width: 160px;
    }

    .price-container .card h3,.price-container .card span{
        display: inline-block;
        width: 100%;
        font-size: 1rem;
        line-height: 2rem;
        opacity: 0;
        }
        .card .card-data button{
            padding: 5px 30px;
            border-radius: 10px;
            background-color: #FEE715FF;
            margin: 10px;
            opacity: 0;
        }
}

/* media query for mobile  */
@media screen and (max-width: 400px){
    .price-container{
        display: flex;
        flex-direction: column;
        grid-gap: 2.5rem 1.5rem;
        gap: 2.5rem 1.5rem;
    }
    .price-container .card{
        width: 200px;
        height: 180px;
    }
    .price-container .card img{
        width: 180px;
    }
    .price-container .card h3,.price-container .card span{
    display: inline-block;
    width: 100%;
    font-size: 1rem;
    line-height: 2rem;
    opacity: 0;
    }
    .card-data{
        text-align: center;
        -webkit-transform: translateY(6rem);
                transform: translateY(6rem);
    }
    .card .card-data button{
        padding: 10px 30px;
        border-radius: 10px;
        background-color: #FEE715FF;
        margin: 10px;
        opacity: 0;
    }
}
.footer{
    box-sizing: border-box;
    position: absolute;
    background-color:#101820FF;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    justify-content: center;
    align-items: center;
    padding: 50px 20px;
    border-top-right-radius: 50px;
    border-top-left-radius: 50px;
    color: white;
}

.footer .col{
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    height: 100%;
    padding: 20px;
}

/* footer headings  */
h2{
    margin-bottom: 20px;
}

.footer .col a .logo{
    font-size: 200px;
}

.footer ul li{
    display: inline-block;
    list-style: none;
    cursor: pointer;
    margin-bottom: 20px;
    width: 100%;
}



.footer .col ul li a{
    list-style: none;
    text-decoration: none;
    font-size: 15px;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -ms-transition: all 0.5s;
    -o-transition: all 0.5s;
}

.footer .col h3{
    font-weight: 300;
    font-size: 1rem;
    line-height: 1.6;
}

.footer .col .social{
    display: flex;
}

.footer .col .social i{
    padding: 8px;
    font-size: 25px;
    color: #101820FF;
    background-color: #FEE715FF;
    border-radius: 50px;
    transition: all 0.5s;
    margin: 0px 10px;
}

/* hover  */

.footer .col .social i:hover{
    color: #FEE715FF;
    background-color: white;
}

.footer ul .links :hover{
    margin-left: 10px;
    color: #606060FF;
}

/* media query for ipad  */
@media screen and (max-width: 768px) {
    /* footer headings  */
    h2{
        font-size: 15px;
    }
    .footer .col ul li a{
        font-size: 12px;
    }
    .footer .col h3{
        font-size: 0.6rem;
    }
    .footer .col a .logo{
        font-size: 100px;
    }
}

/* media query for mobile  */
@media screen and (max-width: 400px){
    .footer{
        display: block;
    }
    .footer .col{
        box-sizing: border-box;
        margin: 0;
        padding: 20px 0px;
        text-align: center;
    }
    /* footer headings  */
    h2{
        font-size: 20px;
    }
    .footer .col ul{
        width: 100%;
    }
}
